
html,
body {
  margin: 0;
  padding: 0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
[header] {
  height: 450px;
  background-color: #27ae60;
  color: #fff;
}
[header-logo-line] {
  min-width: 768px;
  max-width: 1200px;
  margin: 0px auto;
  height: 70px;
  padding: 30px 0;
}
[header-logo-line] img {
  float: left;
  width: 42px;
}
[header-logo-line] [logo-txt] {
  float: left;
  font-size: 36px;
  margin-left: 10px;
}
[header-list] {
  float: right;
  width: 300px;
  margin-top: 18px;
}
[header-list] a {
  color: #eee;
  text-decoration: none;
  margin-left: 20px;
}
[header-list] a:hover {
  color: #fff;
}
[big-title] {
  text-shadow: -5px 5px 0 rgba(0, 0, 0, .1);
  font-size: 56px;
}
[mid-desc] {
  font-size: 24px;
  margin-top: 6px;
  font-weight: 200;
  color: #eee;
  margin-bottom: 10px;
}
[sarch-input] {
  -webkit-box-shadow: none;
  box-shadow: none;
  font-size: 16px;
  padding: 13px 30px;
  border-radius: 0;
  height: auto;
  text-align: center;
  border-color: transparent;
  width: 680px;
}
[mid-sta] {
  margin-top: 10px;
}



